#calcContainer {
    border: 2px solid black;
    width: 300px;
    margin: 0 auto;
}

#screenInput {
    height: 50px;
    border: 1px solid #ccc;
    margin-bottom: 10px;
    text-align: right;
    padding: 10px;
    font-size: 24px;
}

#numbers {
    display: flex;
    flex-wrap: wrap;
}

#numbers button,
#operators button {
    width: 40px;
    height: 40px;
    border: 1px solid #ccc;
    margin: 5px;
    font-size: 24px;
}

#operators {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 10px;
}

#equal,
#clear {
    width: 135px;
    height: 70px;
    margin: 5px;
    font-size: 24px;
}
img{
    width: 200px;
}